const sixthBlogText = "The math library started off as me curious about how the fast math libraries were written. I think I was just waiting for one of my ML models to train, looking at the epoch numbers go up and thinking about the sheer number of calculations that were happening every second. So I went and read up on the numpy (python) and Eigen (c++) source files. They really be zooooomin. \n\n I was procrastinating doing leetcode and thought why not implement my own version? Most of my numerical math I did in my numerical analysis classes, all of the code was in matlab. I initially thought to directly start writing in c++, which I tried but almost immediately decided to go with python for a few reasons. So many of the efficient math tricks in matlab, i know how to implement in python, whereas i was barely comfortable making very basic c++ programs. It's also a lot easier to initially test my algorithms/functions because it's python, easier to write and test at the cost of performance of course. \n\n I started the python library with 3 basic modules in mind. 1) Iterative System Solvers (conjugate gradient, jacobi, gradient descent, gauss-seidel etc) 2) Basic Linear Algebra (Matrix-Matrix and Matrix-Vector products, dot products, outer products, various norms etc) 3) Eigen Pair Methods (power method, subspace iteration etc). I ended up adding a 4th module Decompositions as well (currently contains Fast Fourier Transform and PCA). The python library is up and works quite well in my opinion. The algorithms are very efficient in terms of needed Matrix-Vector Products which in turn should make us efficient in the FLOP’s sense as well.\n\n I'm currently working on 2 things in parallel. I'm trying to make bindings for my python library which should allow me to run the compute intensive portion of my python code in C or C++. At the same time I've started writing the library out in c++ as well. Currently it just has Matrix/Vector objects that you can do basic operations with. It was actually fun, I had to use these things called templates. I think I'll do a  mini post on templates sometime soon."
const eigthBlogText = "In 1914, Europe was tense and split into 2 power blocs: The triple alliance and the triple entente. The triple alliance contained Germany, Austria-Hungary and Italy, with the addition of the Ottoman Empire later on, they were also known as the central powers. The triple entente consisted of Great Britain, France and Russia. With a lot more countries joining in later, these 3 formed the core of the allied powers. If you take a birds eye view of the two power blocs we see that the triple alliance was getting weaker by day while the triple entente was getting stronger. The reasons for this can generally be put into two categories: betrayal by Italy and the problems that Austria-Hungary faced. \n\nThe Italians were disappointed with their losses in the 1890’s in Africa and believe that the Germans should have helped them out more. With a little wooing from France, spearheaded by declasse (Foreign Minister in france at that time), in 1902 the italians agreed to stay neutral in a franco-german war. This effectively makes them a member of the triple alliance in name only. The Italians later sold out the triple alliance again, this time to the russians. \n\nThe problems that Austria-Hungary faced were of three types: Nationality, Military, and Alliances. The biggest issue with nationalities was the clash of Hungarians with other minorities. Out of the 12 million people only about 4 million were Hungarian with the other 8 million split amongst various peoples like the Serbs, Czechs, Poles, Romanians etc. This resulted in most people of other nationalities being oppressed by the Hungarians. This only added fuel to the fire regarding the disunity amongst the people of the empire and further empowered the Serbs to take even more extreme measures against the monarchy, which is seen when Gavrilo Princip assassinated the heir to the habsburg monarchy in June 1914 in Sarajevo, triggering what was later called World War 1.\n\nThe main military issue was that before the war began the Austrians were made aware of the fact that they had a spy in their ranks who had been leaking war plans to the Russians, Alfred Redl. Through some assistance from the Germans, the chief after Redl discovers what Redl had been doing. Once discovered he is given a revolver which he uses to commit suicide. The most mind boggling thing about this whole affair was the fact that Redl was never questioned. The biggest crime here was not getting more info from Redl. No one knows exactly what Redl gave to the Russians but he gave them the Austrian war plans against Serbia and in the case of Russian fortifications he provided the Austrians with false information. Now this was only 6 months before the war when they discovered that the plans had been leaked and there was no time now to change them enough.\n\nAustria-Hungary in 1914 had only one ally, the Germans. Austria-Hungary had not only lost most of its alliances by the eve of the world war but it was actually in the position of having war plans against some of their previous allies. It had plans against Serbia and Russia, as well as against Italy and Romania. Italy is still part of the Triple alliance on paper so this was an even bigger deal as Italy was still technically their ally. They then found themselves not even being able to trust in their sole ally Germany. The Austrians had their trust in the Germans dampened after the lack of support the Germans showed to the Austrians in the 2nd Balkan war in 1913. Austria wanted to aid Bulgaria in their war against the Serbs - Austria's mortal enemy. This was blocked by the Germans in fear of a greater war breaking out.\n\n The Austrians rightfully thought that the serbian government was involved in the assassination attempt and wanted to punish the serbs for it. Russia though didn't have a formal alliance or treaty with Serbia, was having its grand visions of pan-slavism. Due to this they believed not defending Serbia would make them lose all credibility in the Balkans and that's something Russia wanted to avoid greatly, enough to go to war. France and Russia had a military pact going on since the early 1890s. The Germans were then sure that France would come to Russia’s aid and so preemptively attacked which drew France into the War. \n\n"
